.noselection, button {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

button {
  outline: none;
  border: none;
  border-radius: var(--border-radius-hard);
  background-color: var(--base-blue-color-lightened);
  color: var(--base-end-color);
  padding: 1rem 2rem;
  font-size: 1.25rem;
  cursor: pointer;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}
button img {
  height: 1rem;
}

button.selected, .button.selected {
  font-weight: 600 !important;
  color: var(--base-blue-color-lightened);
  border-color: var(--base-blue-color-lightened) !important;
}

.flex-column-wrap, .flex-column, .flex-row-wrap, .flex-row, .flex {
  display: flex;
}

.flex-row-wrap, .flex-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.flex-column-wrap, .flex-column {
  flex-direction: column;
}

.flex-column-wrap, .flex-row-wrap {
  flex-wrap: wrap;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--base-color-lightened-3);
  font-family: "Roboto", serif;
  -webkit-tap-highlight-color: #fff0 !important;
}

body * {
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

*::-webkit-scrollbar {
  width: 0.5rem;
}

*::-webkit-scrollbar-track {
  background: var(--base-color-lightened-6);
  margin: 1.5rem 0;
  border-radius: var(--border-radius);
}

*::-webkit-scrollbar-thumb {
  background-color: var(--base-color-lightened-3);
  border-radius: var(--border-radius);
  border: var(--border-width) solid var(--base-color-lightened-3);
}

html {
  font-size: 16px;
  -webkit-text-size-adjust: none;
}

html {
  --theme: "default";
  --base-background-color: #f8f9fa;
  --base-color-static: #000;
  --base-color: #000;
  --base-color-lightened-1: #4a5d75;
  --base-color-lightened-2: #98a0ab;
  --base-color-lightened-3: #b9bfc7;
  --base-color-lightened-4: #c1cdd5;
  --base-color-lightened-5: #dae1e6;
  --base-color-lightened-6: #e6eaef;
  --base-color-lightened-65: #f3f5f7;
  --base-color-lightened-7: #f8f9fa;
  --base-end-color-static: #fff;
  --base-end-color: #fff;
  --base-blue-color: #5963ff;
  --base-blue-color-lightened: #7178e3;
  --base-red-color: #eb4e39;
  --base-green-color: #86cf2d;
  --base-font-color: #3e4855;
  --base-font-color-secondary: #98a0ab;
  --base-font-color-secondary-half: #6b7480;
  --box-shadow: 0px 4px 12px #33333322;
  --box-shadow-dark: 0px 6px 12px #3333;
  --border-radius: 0.9375rem;
  --border-radius-hard: calc(var(--border-radius) * 2);
  --border-radius-hardest: calc(var(--border-radius) * 10);
  --border-color: #e6eaef;
  --border-color-dark-only: #00000000;
  --border-width: 0.1875rem;
  --border-width-thin: calc((var(--border-width) / 3) * 4);
  --border-width-thick: calc(var(--border-width) * 2);
  --base-filter-brightness: unset;
}
html.dark {
  --theme: "dark";
  --base-background-color: #1e1e1e;
  --base-color: f.theme-transform(#000);
  --base-color-lightened-1: #4a5d75;
  --base-color-lightened-2: #98a0ab;
  --base-color-lightened-3: #b9bfc7;
  --base-color-lightened-4: #c1cdd5;
  --base-color-lightened-5: #484b4d;
  --base-color-lightened-6: #383b3d;
  --base-color-lightened-65: #252526;
  --base-color-lightened-7: #252526;
  --border-color: #98a0ab;
  --border-color-dark-only: #00000000;
  --base-end-color: #252526;
  --base-font-color: #fff;
  --base-font-color-secondary: #98a0ab;
  --base-font-color-secondary-half: #6b7480;
  --base-filter-brightness: 5;
}

html,
body {
  background-color: var(--base-background-color);
  overflow: scroll;
  overflow-y: auto !important;
  overflow-x: hidden !important;
}

input {
  text-align: left;
  height: calc(2.5rem - 4px);
  padding: 0 0 0 2rem;
  border-radius: var(--border-radius-hard);
  background-color: var(--base-end-color);
}

h1 {
  font-weight: 500;
}

textarea {
  border: var(--border-width) solid var(--border-color);
  border-radius: var(--border-radius);
  background-color: var(--base-end-color);
}

BR,
br {
  height: 0;
  margin: 0.625rem 0;
}

.button {
  cursor: pointer;
}
button:hover {
  filter: invert(0.05);
}

.block {
  display: block;
}

.inline {
  display: inline-block;
}

@media (max-width: 255em) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 127em) {
  html {
    font-size: 80%;
  }
}
@media (max-width: 102em) {
  html {
    font-size: 75%;
  }
}
@media (max-width: 93em) {
  html {
    font-size: 62.5%;
  }
}
@media (max-width: 83.5em) {
  html {
    font-size: 57.5%;
  }
}
@media (max-width: 63em) {
  html {
    font-size: 48%;
  }
}