@use './functions' as f;

@mixin themes {
  html {
    --theme: "default";
    --base-background-color: #f8f9fa;
    --base-color-static: #000;
    --base-color: #000;
    --base-color-lightened-1: #4a5d75;
    --base-color-lightened-2: #98a0ab;
    --base-color-lightened-3: #b9bfc7;
    --base-color-lightened-4: #c1cdd5;
    --base-color-lightened-5: #dae1e6;
    --base-color-lightened-6: #e6eaef;
    --base-color-lightened-65: #f3f5f7;
    --base-color-lightened-7: #f8f9fa;
    --base-end-color-static: #fff;
    --base-end-color: #fff;
  
    --base-blue-color: #5963ff;
    --base-blue-color-lightened: #7178e3;
    --base-red-color: #eb4e39;
    --base-green-color: #86cf2d;
    --base-font-color: #3e4855;
    --base-font-color-secondary: #98a0ab;
    --base-font-color-secondary-half: #6b7480;
  
    --box-shadow: 0px 4px 12px #33333322;
    --box-shadow-dark: 0px 6px 12px #3333;
  
    --border-radius: 0.9375rem;
    --border-radius-hard: calc(#{var(--border-radius)} * 2);
    --border-radius-hardest: calc(#{var(--border-radius)} * 10);
    --border-color: #e6eaef;
    --border-color-dark-only: #00000000;
    --border-width: 0.1875rem;
    --border-width-thin: calc((#{var(--border-width)} / 3) * 4);
    --border-width-thick: calc(#{var(--border-width)} * 2);

    --base-filter-brightness: unset;

    &.dark {
      --theme: "dark";
      --base-background-color: #1e1e1e;
      --base-color: f.theme-transform(#000);
      --base-color-lightened-1: #{f.theme-transform(#4a5d75, var(--theme))};
      --base-color-lightened-2: #{f.theme-transform(#98a0ab, var(--theme))};
      --base-color-lightened-3: #{f.theme-transform(#b9bfc7, var(--theme))};
      --base-color-lightened-4: #{f.theme-transform(#c1cdd5, var(--theme))};
      --base-color-lightened-5: #484b4d;
      --base-color-lightened-6: #383b3d;
      --base-color-lightened-65: #252526;
      --base-color-lightened-7: #252526;

      --border-color: #98a0ab;
      --border-color-dark-only: #{f.theme-transform(#00000000, var(--theme), 0.7)};
  
      --base-end-color: #252526;
      --base-font-color: #fff;
      --base-font-color-secondary: #98a0ab;
      --base-font-color-secondary-half: #6b7480;

      --base-filter-brightness: 5;
    }
  }
}
